import fs from 'fs'
import path from 'path'
import YAML from 'yaml'

/**  config/homepage.yaml  */
export interface HomepageConfig {
  site: { name: string }
  header: {
    nav: { label: string; href: string }[]
    login: string
    ariaFontSize: string
  }
  topBanner: {
    message: string
    copyButton: string
    toastCopied: string
    ariaClose: string
  }
  hero: {
    badge: string
    headlineBefore: string
    headlineHighlight: string
    headlineAfter: string
    subtitle: string
    ctaPrimary: string
    avatars: { seed: string; alt: string }[]
    stats: string
    tooltipDataPrefix: string
    ariaDataInfo: string
  }
  features: {
    sectionTitle: string
    sectionSubtitle: string
    list: { title: string; desc: string; icon: string }[]
  }
  about: {
    logo: string
    logoAlt: string
    name: string
    nameEn?: string
    copyright: string[]
    columns: {
      title: string
      items: {
        title: string
        desc?: string
        href?: string
        linkText?: string
        text?: string
        links?: { label: string; href: string }[]
      }[]
    }[]
  }
  themeToggle: {
    ariaLight: string
    ariaDark: string
    ariaPlaceholder: string
  }
}

/**  config/global.yaml  */
export interface GlobalConfig {
  theme: {
    /** light | dark | system */
    default: 'light' | 'dark' | 'system'
  }
}

const homepagePath = path.join(process.cwd(), 'src', 'config', 'homepage.yaml')
const globalPath = path.join(process.cwd(), 'src', 'config', 'global.yaml')

/**  YAML  */
export function getHomepageConfig(): HomepageConfig {
  const raw = fs.readFileSync(homepagePath, 'utf-8')
  return YAML.parse(raw) as HomepageConfig
}

/**  YAML  */
export function getGlobalConfig(): GlobalConfig {
  const raw = fs.readFileSync(globalPath, 'utf-8')
  return YAML.parse(raw) as GlobalConfig
}
